import React, { useEffect, useState } from 'react'


const LoggedUser: React.FC = () => {
  const [userName, setUserName] = useState('Natava sa ....');

  useEffect(() => {
    // Funkcia na zskanie mena prihlsenho pouvatea zo SharePoint REST API
    const fetchUserName = async () => {
      try {
        const response = await fetch(`${_spPageContextInfo.webAbsoluteUrl}/_api/web/currentuser`, {
          method: 'GET',
          headers: {
            'Accept': 'application/json;odata=verbose',
            'Content-Type': 'application/json;odata=verbose',
          },
          credentials: 'same-origin'  // Dleit pre autentizciu v SharePoint prostred
        });

        if (!response.ok) {
          throw new Error(`SharePoint API call failed with status ${response.status}`);
        }

        const data = await response.json();
        console.log(data.d);
        
        setUserName(data.d.Title); // Nastavenie mena pouvatea natanho z API
      } catch (error) {
        console.error('Error fetching user data:', error);
        setUserName('Nepodarilo sa nata meno pouvatea');
      }
    };

    fetchUserName();
  }, []);

  return <div className="content-container"><i>Prihlsen:</i> <b>{userName}</b></div>;
};

export default LoggedUser;